
.home .left-bar {
  width: 75%;
}
.home .left-bar .classify {
    width: 100%;
    background: #f6f6f6;
}
.home .left-bar .classify span {
      display: inline-block;
      padding: 5px 8px;
      margin: 15px 10px;
      border-radius: 4px;
      font-size: 13px;
      color: #369219;
      cursor: pointer;
}
.home .left-bar .classify span.active {
      background: #369219;
      color: #fff;
}
.home .left-bar .content {
    width: 100%;
    margin: 0;
}
.home .left-bar .content li {
      width: 100%;
      height: 51px;
      border-top: 1px solid #f0f0f0;
      background: #fff;
      clear: both;
      overflow: hidden;
}
.home .left-bar .content li .head {
        display: inline-block;
        width: 30px;
        height: 30px;
        margin: 10px 5px 0 10px;
}
.home .left-bar .content li .comment {
        width: 8%;
        line-height: 51px;
        font-size: 14px;
        color: #9e78c0;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.home .left-bar .content li .comment .total-comment {
          color: #b4b4b4;
}
.home .left-bar .content li .tag {
        margin-right: 4px;
        line-height: 51px;
}
.home .left-bar .content li .tag span {
          padding: 2px 5px;
          border-radius: 2px;
          background: #369219;
          color: #fff;
}
.home .left-bar .content li .tag .share {
          background: #e5e5e5;
}
.home .left-bar .content li .tag .ask {
          background: #e5e5e5;
}
.home .left-bar .content li .title {
        max-width: 70%;
        line-height: 51px;
}
.home .left-bar .content li .title a {
          display: inline-block;
          width: 100%;
          font-size: 15px;
          color: #000;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
}
.home .left-bar .content li .time {
        padding-right: 1%;
        line-height: 51px;
}
.home .left-bar .content li .time img {
          display: inline-block;
          width: 18px;
          height: 18px;
          margin-right: 20px;
          vertical-align: middle;
          -webkit-transform: translateY(-2px);
                  transform: translateY(-2px);
}
.home .left-bar .content li .time span {
          color: #778087;
}
.home .left-bar .content li:hover {
      background-color: #f6f6f6;
}
.home .left-bar .pages {
    width: 100%;
    padding: 10px;
    background-color: #fff;
    *zoom: 1;
}
.home .left-bar .pages:before, .home .left-bar .pages:after {
      content: "";
      display: table;
}
.home .left-bar .pages:after {
      clear: both;
      overflow: hidden;
}
.home .left-bar .pages ul {
      float: left;
      border: 1px solid #ddd;
      *zoom: 1;
}
.home .left-bar .pages ul:before, .home .left-bar .pages ul:after {
        content: "";
        display: table;
}
.home .left-bar .pages ul:after {
        clear: both;
        overflow: hidden;
}
.home .left-bar .pages ul li {
        float: left;
        width: 30px;
        height: 30px;
        background-color: #fff;
        line-height: 30px;
        text-align: center;
        color: #778087;
        cursor: pointer;
}
.home .left-bar .pages ul li + li {
        border-left: 1px solid #ddd;
}
.home .left-bar .pages ul li.active {
        background-color: rgba(0, 0, 0, 0.1);
}
.home .right-bar {
  width: 23%;
}
.home .right-bar .right-content {
    width: 100%;
    margin-bottom: 13px;
    border-radius: 2px;
    background: #fff;
}
.home .right-bar h2 {
    background: #f6f6f6;
    line-height: 42px;
    font-size: 12px !important;
    text-indent: 10px;
    color: #1c6132;
}
.home .right-bar .login {
    padding: 10px;
}
.home .right-bar .login h1 {
      margin: 19px 0 24px;
      font-size: 15px !important;
      color: #333;
}
.home .right-bar .login p {
      color: #333;
}
.home .right-bar .login p a {
        color: #778087;
}
.home .right-bar .login .login-github {
      width: 60%;
      height: 34px;
      margin-top: 10px;
      border: none;
      border-radius: 2px;
      background-color: #7597d4;
      font-size: 15px;
      color: #fff;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
}
.home .right-bar .adv {
    width: 100%;
    padding: 10px;
}
.home .right-bar .adv img {
      width: 100%;
}
.home .right-bar .adv a {
      display: inline-block;
}
.home .right-bar .adv a + a {
      margin-top: 10px;
}
.home .right-bar .topic ul {
    padding: 0 10px 30px;
}
.home .right-bar .topic ul li {
      width: 100%;
      margin-top: 20px;
}
.home .right-bar .topic ul li a {
        display: inline-block;
        width: 100%;
        color: #778087;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.home .right-bar .rank ul {
    padding: 0 10px 30px;
}
.home .right-bar .rank ul li {
      margin-top: 20px;
      font-size: 13px;
      color: #778087;
}
.home .right-bar .rank ul li a {
        color: #778087;
}
.home .right-bar .link a {
    display: block;
    padding: 10px;
    margin-top: 5px;
}
.home .right-bar .link img {
    width: 70%;
}
.home .right-bar .code .scan-code {
    width: 60%;
    margin: 10px auto;
}
.home .right-bar .code p {
    padding-bottom: 20px;
    text-align: center;
}



