
.container {
  width: 90%;
  margin: 0 auto;
  *zoom: 1;
}
.container .fl {
    float: left;
}
.container .fr {
    float: right;
}
.container:before, .container:after {
    content: "";
    display: table;
}
.container:after {
    clear: both;
    overflow: hidden;
}
* {
  box-sizing: border-box;
}
header {
  padding-bottom: 2px;
  background-color: #1c6132;
}
header input:focus {
    outline: none;
}
header button:focus {
    outline: none;
}
header .logo-bar {
    width: 100%;
    padding: 2px 0;
}
header .logo-bar .logoname {
      margin-top: 1.8%;
      font-size: 2rem;
      font-weight: bold;
      color: #fff;
}
header .logo-bar .logo {
      display: inline-block;
      width: 4rem;
      margin-top: 8px;
      vertical-align: middle;
}
header .logo-bar .search-box {
      position: relative;
      width: 235px;
      height: 26px;
      margin: 1.8% 0 0 25px;
      border-radius: 12px;
      background: #4f9639;
      text-indent: 2%;
}
header .logo-bar .search-box .search {
        width: 100%;
        height: 26px;
        border: none;
        background-color: transparent;
}
header .logo-bar .search-box .search-btn {
        position: absolute;
        top: 0;
        left: 0;
        width: 10%;
        height: 26px;
        border: none;
        background-color: transparent;
}
header .logo-bar .main-nav {
      margin-top: 1.8%;
}
header .logo-bar .main-nav li {
        float: left;
        margin-left: 30px;
}
header .logo-bar .main-nav li a {
          font-size: 15px;
          color: #ccc;
}
header .logo-bar .fa-bars {
      margin-top: 5px;
      font-size: 20px;
      color: #fff;
}
header .sub-main-nav li {
    line-height: 45px;
    text-align: center;
}
header .sub-main-nav li a {
      font-size: 15px;
      color: #fff;
}
header .sub-main-nav li + li {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
}
.main {
  padding: 15px 0;
  background: #e1e1e1;
}
.main .back-to-top {
    position: fixed;
    right: 0;
    top: 80%;
    width: 25px;
    padding: 1% 0.5% 1% 0.5%;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    background-color: #fff;
    cursor: pointer;
}
footer {
  padding: 27px 0;
}
footer .download {
    margin-bottom: 5px;
    color: #ababbd;
}
footer .download a {
      font-size: 15px;
      color: #333;
}
footer .info {
    font-size: 15px;
    color: #b2bdbd;
}
footer .info img {
      display: inline-block;
      vertical-align: middle;
}
footer .info img.cow {
      width: 113px;
      height: auto;
}
footer .info a {
      color: #0096da;
}
@media only screen and (min-width: 320px) {
header .logo-bar .logo {
    display: none;
}
header .logo-bar .logoname {
    display: none;
}
header .logo-bar .search-box {
    display: none;
}
header .logo-bar .main-nav {
    display: none;
}
.right-bar {
    display: none;
}
.left-bar {
    width: 100% !important;
}
.left-bar .time {
      display: none;
}
.left-bar .comment {
      display: none;
}
.left-bar .tag {
      display: none;
}
}
@media only screen and (min-width: 480px) {
header .logo-bar .logo {
    display: none;
}
header .logo-bar .logoname {
    display: none;
}
header .logo-bar .search-box {
    display: none;
}
header .logo-bar .main-nav {
    display: none;
}
.right-bar {
    display: none;
}
.left-bar {
    width: 100% !important;
}
.left-bar .time {
      display: none;
}
.left-bar .comment {
      display: none;
}
.left-bar .tag {
      display: block;
}
}
@media only screen and (min-width: 768px) {
header .logo-bar .logo {
    display: block;
    width: 4rem;
}
header .logo-bar .logoname {
    display: block;
}
header .logo-bar .search-box {
    display: block;
}
header .logo-bar .main-nav {
    display: none;
}
header .logo-bar .fa-bars {
    display: block;
}
.right-bar {
    display: none;
}
.left-bar {
    width: 100% !important;
}
.left-bar .time {
      display: block;
}
.left-bar .comment {
      display: block;
}
.left-bar .tag {
      display: block;
}
}
@media only screen and (min-width: 992px) {
header .logo-bar .logo {
    display: block;
    width: 4rem;
}
header .logo-bar .logoname {
    display: block;
}
header .logo-bar .search-box {
    display: block;
}
header .logo-bar .main-nav {
    display: block;
    float: right !important;
}
header .logo-bar .fa-bars {
    display: none;
}
header .sub-main-nav {
    display: none !important;
    height: 0;
}
.right-bar {
    display: block;
}
.left-bar {
    width: 75% !important;
}
.left-bar .time {
      display: block;
}
.left-bar .comment {
      display: block;
}
.left-bar .tag {
      display: block;
}
}



