
.for-title .left-bar .author {
  border-bottom: 1px solid #e1e1e1;
  background-color: #fff;
}
.for-title .left-bar .author .author-head {
    margin: 0 0 0 1.5%;
    padding: 2% 0;
    line-height: 130%;
    font-size: 22px;
    font-weight: 700;
}
.for-title .left-bar .author .author-head span {
      padding: 2px 5px;
      border-radius: 2px;
      background: #80bd01;
      color: #fff;
}
.for-title .left-bar .author .author-head .share {
      background: #e5e5e5;
}
.for-title .left-bar .author .author-head .ask {
      background: #e5e5e5;
}
.for-title .left-bar .author .author-info {
    *zoom: 1;
}
.for-title .left-bar .author .author-info:before, .for-title .left-bar .author .author-info:after {
      content: "";
      display: table;
}
.for-title .left-bar .author .author-info:after {
      clear: both;
      overflow: hidden;
}
.for-title .left-bar .author .author-info li {
      float: left;
      color: #838383;
}
.for-title .left-bar .author .author-info li i {
        -webkit-transform: scale(0.4);
                transform: scale(0.4);
}
.for-title .left-bar .markdown-text {
  width: 100%;
  padding: 1.5%;
  background-color: #fff;
  line-height: 2em;
}
.for-title .left-bar .markdown-text img {
    max-width: 100%;
}
.for-title .left-bar .comments {
  margin-top: 20px;
  background: #fff;
}
.for-title .left-bar .comments li.messages-head {
    background-color: #f6f6f6;
    line-height: 30px;
    font-size: 14px;
    color: #1c6132;
}
.for-title .left-bar .comments li {
    padding: 0 1%;
    border-top: 1px solid #f0f0f0;
    *zoom: 1;
}
.for-title .left-bar .comments li:before, .for-title .left-bar .comments li:after {
      content: "";
      display: table;
}
.for-title .left-bar .comments li:after {
      clear: both;
      overflow: hidden;
}
.for-title .left-bar .comments li .head {
      width: 4%;
      margin: 1.5% 15px 0 0;
      border-radius: 2px;
}
.for-title .left-bar .comments li .messages {
      width: 91%;
      margin-top: 1.8%;
}
.for-title .left-bar .comments li .messages .messages-title {
        width: 100%;
        color: #666;
        *zoom: 1;
}
.for-title .left-bar .comments li .messages .messages-title:before, .for-title .left-bar .comments li .messages .messages-title:after {
          content: "";
          display: table;
}
.for-title .left-bar .comments li .messages .messages-title:after {
          clear: both;
          overflow: hidden;
}
.for-title .left-bar .comments li .messages .messages-title a {
          color: #000;
}
.for-title .left-bar .comments li .messages .messages-title span {
          color: #08c;
}
.for-title .left-bar .comments li .messages .messages-title span.writer {
          padding: 0 2px;
          background-color: #6ba44e;
          color: #fff;
}
.for-title .left-bar .comments li .messages .messages-title i.fa-circle {
          -webkit-transform: scale(0.6);
                  transform: scale(0.6);
}
.for-title .left-bar .comments li .messages .messages-title .ups {
          font-size: 15px;
          color: gray;
}
.for-title .right-bar {
  width: 23%;
}
.for-title .right-bar .right-content {
    width: 100%;
    margin-bottom: 13px;
    border-radius: 2px;
    background: #fff;
}
.for-title .right-bar h2 {
    background: #f6f6f6;
    line-height: 42px;
    font-size: 12px !important;
    text-indent: 10px;
    color: #1c6132;
}
.for-title .right-bar .author-card {
    padding-bottom: 2%;
}
.for-title .right-bar .author-card .avatar {
      font-size: 18px;
}
.for-title .right-bar .author-card .avatar img {
        display: inline-block;
        width: 20%;
        vertical-align: middle;
}
.for-title .right-bar .author-card .avatar a {
        color: #778087;
}
.for-title .right-bar .author-card .score {
      text-indent: 3%;
      font-size: 14px;
}
.for-title .right-bar .author-card .words {
      font-style: italic;
}
.for-title .right-bar .adv {
    width: 100%;
    padding: 10px;
}
.for-title .right-bar .adv img {
      width: 100%;
}
.for-title .right-bar .adv a {
      display: inline-block;
}
.for-title .right-bar .adv a + a {
      margin-top: 10px;
}
.for-title .right-bar .recent-topics ul {
    padding: 0 10px 20px;
}
.for-title .right-bar .recent-topics ul li {
      width: 100%;
      margin-top: 10px;
}
.for-title .right-bar .recent-topics ul li p {
        display: inline-block;
        width: 100%;
        color: #778087;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        cursor: pointer;
}
.for-title .right-bar .topic ul {
    padding: 0 10px 20px;
}
.for-title .right-bar .topic ul li {
      width: 100%;
      margin-top: 10px;
}
.for-title .right-bar .topic ul li a {
        display: inline-block;
        width: 100%;
        color: #778087;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}



