
.for-about {
  padding: 15px 0;
  background: #e1e1e1;
}
.for-about .left-bar {
    width: 75%;
    background: #fff;
}
.for-about .left-bar .location {
      width: 100%;
      background: #f6f6f6;
      line-height: 40px;
      color: #80bd01;
      text-indent: 1%;
}
.for-about .left-bar .location a {
        color: #80bd01;
}
.for-about .left-bar .location span {
        color: #999;
}
.for-about .left-bar .content {
      width: 100%;
      padding: 15px 2%;
}
.for-about .left-bar .content .left-content + .left-content {
        margin-top: 20px;
}
.for-about .left-bar .content h1 {
        border-bottom: 1px solid #eee;
        line-height: 40px;
        font-size: 24px;
}
.for-about .left-bar .content p {
        font-size: 15px;
        line-height: 2em;
        letter-spacing: 1.5px;
}
.for-about .left-bar .content p a {
          font-size: 15px;
          color: #08c;
}
.for-about .left-bar .content .link h1 {
        margin-bottom: 10px;
}
.for-about .left-bar .content .link img {
        display: inline-block;
        width: 30%;
}
.for-about .left-bar .content .pho p {
        margin-top: 5px;
}
.for-about .left-bar .content .pho p a {
          color: #778087;
}
.for-about .right-bar {
    width: 23%;
}
.for-about .right-bar .right-content {
      width: 100%;
      margin-bottom: 13px;
      border-radius: 2px;
      background: #fff;
}
.for-about .right-bar h2 {
      background: #f6f6f6;
      line-height: 42px;
      font-size: 12px !important;
      text-indent: 10px;
      color: #333;
}
.for-about .right-bar .login {
      padding: 10px;
}
.for-about .right-bar .login h1 {
        margin: 19px 0 24px;
        font-size: 15px !important;
        color: #333;
}
.for-about .right-bar .login p {
        color: #333;
}
.for-about .right-bar .login p a {
          color: #778087;
}
.for-about .right-bar .login button {
        width: 60%;
        height: 34px;
        margin-top: 10px;
        border: none;
        border-radius: 2px;
        background-color: #5bc0de;
        font-size: 15px;
        color: #fff;
        cursor: pointer;
}
.for-about .right-bar .adv {
      width: 100%;
      padding: 10px;
}
.for-about .right-bar .adv img {
        width: 100%;
}
.for-about .right-bar .adv a {
        display: inline-block;
}
.for-about .right-bar .adv a + a {
        margin-top: 10px;
}
.for-about .right-bar .link a {
      display: block;
      padding: 10px;
      margin-top: 5px;
}
.for-about .right-bar .link img {
      width: 70%;
}
.for-about .right-bar .code .scan-code {
      width: 60%;
      margin: 10px auto;
}
.for-about .right-bar .code p {
      padding-bottom: 20px;
      text-align: center;
}



