
.for-login {
  padding: 15px 0;
  background: #e1e1e1;
}
.for-login .left-bar {
    width: 75%;
    background: #fff;
}
.for-login .left-bar .location {
      width: 100%;
      background: #f6f6f6;
      line-height: 40px;
      color: #999;
      text-indent: 1%;
}
.for-login .left-bar .location a {
        color: #80bd01;
}
.for-login .left-bar .location span {
        color: #999;
}
.for-login .left-bar .sign-up {
      width: 100%;
      background: #fff;
      padding: 50px 0;
}
.for-login .left-bar .sign-up form .info {
        margin-bottom: 10px;
}
.for-login .left-bar .sign-up form .info label {
          display: inline-block;
          width: 20%;
          margin-right: 2%;
          font-size: 14px;
          color: #000;
          text-align: right;
}
.for-login .left-bar .sign-up form .info input {
          width: 30%;
          height: 30px;
}
.for-login .left-bar .sign-up form .ctrl {
        margin: 30px 0 0 22%;
        line-height: 34px;
}
.for-login .left-bar .sign-up form .ctrl .sign {
          float: left;
          width: 7%;
          height: 34px;
          border: none;
          border-radius: 3px;
          background-color: #08c;
          color: #fff;
}
.for-login .left-bar .sign-up form .ctrl .sign-github {
          float: left;
          width: 20%;
          height: 34px;
          margin: 0 2% 0 0.5%;
          border: none;
          border-radius: 3px;
          background-color: #5bc0de;
          color: #fff;
}
.for-login .left-bar .sign-up form .ctrl .forget {
          font-size: 14px;
          color: #778087;
}
.for-login .to-login {
    margin-bottom: 100px;
}
.for-login .right-bar {
    width: 23%;
}
.for-login .right-bar .right-content {
      width: 100%;
      margin-bottom: 13px;
      border-radius: 2px;
      background: #fff;
}
.for-login .right-bar .right-content h2 {
        background: #f6f6f6;
        line-height: 42px;
        font-size: 12px !important;
        text-indent: 10px;
        color: #333;
}
.for-login .right-bar .right-content .context {
        padding: 5%;
}
.for-login .right-bar .right-content .context p {
          font-size: 14px;
}
.for-login .right-bar .right-content .context ul {
          list-style: disc inside;
}
.for-login .right-bar .right-content .context ul li.title {
            margin: 20px 0 20px;
            font-size: 14px;
            list-style: none;
}
.for-login .right-bar .right-content .context ul li.todo {
            margin-top: 10px;
}



